task-bar {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  left: 0;
  bottom: 0;
  height: var(--TaskbarHeight);
  width: 100%;
  background: var(--TaskbarBkgr);
}
live-apps {
  display: flex;
  flex-direction: row;
  width: 100%;
  border-left: 2px dimgray solid;
  border-right: 2px dimgray solid;
  margin: 2px;
}
live-apps>button {
  display: flex;
  padding-left: 5px;
  padding-right: 5px;
  color: var(--TaskbarFontColor);
  font-size: var(--TaskbarIconSize);
}
live-apps>button>svg{
  width: var(--TaskbarIconSize);
  height: var(--TaskbarIconSize);
}
start-button>button {
  display: flex;
  height: 100%;
  color: var(--StartButtonColor);
  padding-left: 5px;
  padding-right: 5px;
  font-size: var(--TaskbarIconSize);
}
start-button>button:hover {
  color:var(--StartButtonActiveColor);
}
.startButtonActive {
  color:var(--StartButtonActiveColor);
}
.startButtonActive>svg {
  animation: linear 2.5s rotate infinite;
}

start-list {
  position: absolute;
  display: flex;
  flex-direction: row;
  bottom: var(--TaskbarHeight);
  overflow: hidden;
  transition: 1s ease-in-out 0s;
  color: var(--StartMenuFontColor);
  background: var(--StartMenuBkgr);
}

start-list>ul {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding-left: 14px;
  margin-left: var(--StartMenuIconSize);
  width: 100%;
  height:100%;
  overflow-y: auto;
}
start-list>ul>li {
  display: flex;
  flex-direction: row;
  justify-content: start;
  width: 100%;
  height: 100%;
}
start-list>ul>li>button {
  font-size: var(--StartMenuFontSize);
  display: block;
  flex-wrap: nowrap;
  white-space: nowrap;
  text-align: start;
  align-items: start;
  width: 100%;
  padding:2px;
  color: var(--StartMenuFontColor);
}
start-list>ul>li>button>svg{
  width: var(--StartMenuIconSize);
  height: var(--StartMenuIconSize);
}
start-list>div {
  position: absolute;
  bottom:0;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: end;
  width: calc(var(--StartMenuIconSize) + 10px);
  height:100%;
  border-right: 2px gray solid;
  margin-bottom: 2px;
  overflow-x:hidden;
  transition:0.3s;
  transition-delay: 0.3s;
}
start-list>div:hover{
  width:150px;
  background: var(--StartMenuBkgr);
}
start-list>div>button {
  display: flex;
  flex-direction: row;
  justify-content: start;
  width: 150px;
  font-size: var(--StartMenuIconSize);
  padding: 4px;
}
start-list>div>button>svg{
  padding-right:10px;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
